	beforeCreate: function () {
		if(this.$session.exists() && (this.$session.get('user_id')=='' || typeof this.$session.get('user_id')=='undefined')){
			this.$session.destroy();
			this.$router.push('mob001000');
			this.LoginModal= true
		}else if (!this.$session.exists()) {
			this.LoginModal= true
		}else{
			this.user_agent = String(navigator.userAgent).toLowerCase();
			//로그인이 되어있을때
			axios.get('/back/SCR005001/Mob001List',{
				params: {
					search: this.$session.get('user_id')
				}
			})
			.then(response => {
				var authority_001001='N';
				var authority_001002='N';
				var authority_001003='N';
				var authority_001004='N';
				var authority_001005='N';
				var authority_001006='N';
				var authority_001007='N';
				var authority_001008='N';
				for(var x=0;x<response.data.length;x++){
					if(x==0){
						authority_001006=response.data[x].screen_authority;
					}else if(x==1){
						authority_001008=response.data[x].screen_authority;
					}else if(x==2){
						authority_001001=response.data[x].screen_authority;
					}else if(x==3){
						authority_001002=response.data[x].screen_authority;
					}else if(x==4){
						authority_001003=response.data[x].screen_authority;
					}else if(x==5){
						authority_001004=response.data[x].screen_authority;
					}else if(x==6){
						authority_001005=response.data[x].screen_authority;
					}else if(x==7){
						authority_001007=response.data[x].screen_authority;
					}
				}
				if(authority_001001=='Y'){
					this.$router.push('mob001001');
				}else if(authority_001002=='Y'){
					this.$router.push('mob001002');
				}else if(authority_001003=='Y'){
					this.$router.push('mob001003');
				}else if(authority_001004=='Y'){
					this.$router.push('mob001004');
				}else if(authority_001005=='Y'){
					this.$router.push('mob001005');
				}else if(authority_001006=='Y'){
					this.$router.push('mob001006');
				}else if(authority_001008=='Y'){
					this.$router.push('mob001008');
				}else if(authority_001007=='Y'){
					this.$router.push('mob001007');
				}
			})
			.catch(error => {
				console.log(error)
			});
		}
	},
	created() {
		this.setDate();
	},
	mounted() {
		var link = this.$router.app.$route.query.from_location;
		if(String(this.user_agent).indexOf('samsung') > -1 || String(this.user_agent).indexOf('sm-p610') > -1 ){
			(()=>{
				window.__WEBVIEW_BRIDGE__={
					init:function(){
						document.addEventListener('message', function(e){
							ReactNativeWebView.postMessage("MOB001000");
						});
					}
				};
				window.__WEBVIEW_BRIDGE__.init();
			})();
		}
		axios.get('/back/SCR005001/Mob001List',{
				params: {
					search: this.$session.get('user_id')
				}
			})
		.then(response => {
			for(var x=0;x<response.data.length;x++){
				if(x==0){
					this.screen_name_001006=response.data[x].clm_screen_name;
					this.screen_authority_001006=response.data[x].screen_authority;
				}else if(x==1){
					this.screen_name_001008=response.data[x].clm_screen_name;
					this.screen_authority_001008=response.data[x].screen_authority;
				}else if(x==2){
					this.screen_name_001001=response.data[x].clm_screen_name;
					this.screen_authority_001001=response.data[x].screen_authority;
				}else if(x==3){
