		async blockitemsChange(index){
			if(Number(this.BlockHalfItems[index].clm_full_count)<Number(this.BlockHalfItems[index].clm_order_count) ){
				alert('자재 사용 수량이 가용수량을 초과했습니다.');
				this.BlockHalfItems[index].clm_order_count=Number(this.BlockHalfItems[index].clm_full_count);
			}else if(Number(this.BlockHalfItems[index].clm_order_count)<=0){
				alert('자재 사용 수량이 정상적이지 않습니다.');
				this.BlockHalfItems[index].clm_order_count=Number(this.BlockHalfItems[index].clm_full_count);
			}else if(Number(this.BlockHalfItems[index].clm_order_count)%1 !=0){
				alert('자재 사용 수량이 정상적이지 않습니다.');
				this.BlockHalfItems[index].clm_order_count=Number(this.BlockHalfItems[index].clm_full_count);
			}else if(this.BlockHalfItems[index].clm_order_count=='' || typeof this.BlockHalfItems[index].clm_order_count=='undefined'){
				this.BlockHalfItems[index].clm_order_count=Number(this.BlockHalfItems[index].clm_full_count);
			}	
			var sum =0;
			var useable_sum =0;
			for(var x=0; x<this.BlockHalfItems.length;x++){
				sum=sum+Number(String(this.BlockHalfItems[x].clm_order_count).replace(/,/gi, ""));
				useable_sum=useable_sum+Number(String(this.BlockHalfItems[x].clm_full_count).replace(/,/gi, ""));
			}
			var index =Number(sum);
			var cnt =0;
			for(let i=0;i<3;i++){
				if(index%3!=0){
					index=index-1;
					cnt=cnt+1;
				}else{
					break;
				}
			}
			if(Number(this.HalfItems[0].clm_m_usable_count) >= index/3){
				this.HalfItems[0].clm_m_material_count=index/3;
				this.HalfItems[1].clm_m_material_count=index;
			}
			this.HalfItems[1].clm_m_material_count= Number(index);
			this.HalfItems[1].clm_m_usable_count= Number(useable_sum);
			if(this.BlockHalfItems.length>1){
				if(Number(this.BlockHalfItems[this.BlockHalfItems.length-1].clm_order_count)>cnt){
					this.BlockHalfItems[this.BlockHalfItems.length-1].clm_order_count= Number(this.BlockHalfItems[this.BlockHalfItems.length-1].clm_order_count)-cnt;
				}else{
					if(Number(this.BlockHalfItems[this.BlockHalfItems.length-1].clm_order_count)<cnt){
						//cnt ==2  ordercount ==1
						if(this.BlockHalfItems.lenlengthgt>2){
							this.BlockHalfItems[this.BlockHalfItems.length-2].clm_order_count = Number(this.BlockHalfItems[this.BlockHalfItems.length-2].clm_order_count)-1;
							this.BlockHalfItems.splice(this.BlockHalfItems.length-1,1);
						}else{
							alert('자재를 추가해주시기 바랍니다.');
						}
					}else{
						// ordercount == cnt
						this.BlockHalfItems.splice(this.BlockHalfItems.length-1,1);
					}
				}
				sum =0;
				useable_sum=0;
				for(var x=0; x<this.BlockHalfItems.length;x++){
					sum=sum+Number(String(this.BlockHalfItems[x].clm_order_count).replace(/,/gi, ""));
					useable_sum=useable_sum+Number(String(this.BlockHalfItems[x].clm_full_count).replace(/,/gi, ""));
				}
				this.HalfItems[1].clm_m_material_count= Number(sum);
				this.HalfItems[1].clm_m_usable_count= Number(useable_sum);
			}else if(this.BlockHalfItems.length==1){
				var index =Number(this.BlockHalfItems[0].clm_order_count);
				var cnt =0;
				for(let i=0;i<3;i++){
					if(index%3!=0){
						index=index-1;
						cnt=cnt+1;
					}else{
						break;
					}
				}
				if(Number(this.HalfItems[0].clm_m_usable_count) >= index/3){
					this.HalfItems[0].clm_m_material_count=index/3;
					this.HalfItems[1].clm_m_material_count=index;
					this.BlockHalfItems[0].clm_order_count=index;
				}else{
					if(Number(this.BlockHalfItems[0].clm_order_count) >= (Number(this.HalfItems[0].clm_m_material_count)*3)){
					this.HalfItems[0].clm_m_material_count=Number(this.HalfItems[0].clm_m_material_count);
					this.HalfItems[1].clm_m_material_count=(Number(this.HalfItems[0].clm_m_material_count)*3);
					this.BlockHalfItems[0].clm_order_count=Number(this.HalfItems[0].clm_m_material_count)*3;
					}else{
						this.HalfItems[1].clm_m_material_count= Number(this.BlockHalfItems[0].clm_order_count);
						this.HalfItems[1].clm_m_usable_count= Number(this.BlockHalfItems[0].clm_full_count);
					}
				}
			}
		},
