		cutCountChange(value){
			if(this.clm_m3_material_cut_count=='' || typeof this.clm_m3_material_cut_count=='undefined'){
				this.clm_m3_material_cut_count=0;
			}
			if(this.clm_m4_material_cut_count=='' || typeof this.clm_m4_material_cut_count=='undefined'){
				this.clm_m4_material_cut_count=0;
			}
			if(this.clm_m5_material_cut_count=='' || typeof this.clm_m5_material_cut_count=='undefined'){
				this.clm_m5_material_cut_count=0;
			}
			this.clm_m3_material_cut_count = Number(this.clm_m3_material_cut_count );
			this.clm_m4_material_cut_count = Number(this.clm_m4_material_cut_count );
			this.clm_m5_material_cut_count = Number(this.clm_m5_material_cut_count );
			var sum = Number(this.clm_m3_material_cut_count)+Number(this.clm_m4_material_cut_count)+Number(this.clm_m5_material_cut_count);
			if(Number(this.clm_m3_material_count)<sum){
				alert('자재 사용 수량의 합이 자재 가용 수량을 초과했습니다.');
				if(value=='1'){
					this.clm_m3_material_cut_count=Number(this.clm_m3_material_count)-sum+Number(this.clm_m3_material_cut_count);
					// this.clm_m3_material_cut_count=0;
				}else if(value=='2'){
					this.clm_m4_material_cut_count=Number(this.clm_m3_material_count)-sum+Number(this.clm_m4_material_cut_count);
					// this.clm_m4_material_cut_count=0;
				}else if(value=='3'){
					this.clm_m5_material_cut_count=Number(this.clm_m3_material_count)-sum+Number(this.clm_m5_material_cut_count);
					// this.clm_m5_material_cut_count=0;
				}
			}
		},
