		use_count_change(index){
			var tmp_material_order_use_total = 0; //같은 part
			var tmp_material_cube_size = 0; //같은 lotno 
			var exist_yn = 'N';
			var max_index = index;			
			if(this.productItems.length > 0){
				for(var x=0; x<this.productItems.length; x++){
					if(this.productItems[index].clm_part_id == this.productItems[x].clm_part_id){
						tmp_material_order_use_total = Number(tmp_material_order_use_total) + Number(this.productItems[x].clm_use_count);
					}
					if(this.productItems[index].clm_material_lotno == this.productItems[x].clm_material_lotno){
						tmp_material_cube_size = Number(tmp_material_cube_size) + Number(this.productItems[x].clm_use_count);
					}
					
				}
			}
			if(this.productItems[index].clm_use_count=='' || Number(this.productItems[index].clm_use_count)<=0 ||typeof this.productItems[index].clm_use_count=='undefined' ){
				if(Number(this.productItems[index].clm_m_before_product_count) == '' || isNaN(this.productItems[index].clm_m_before_product_count)==true){
					if(Number(this.productItems[index].clm_material_full_count)-Number(tmp_material_cube_size)+Number(this.productItems[index].clm_use_count)>Number(this.productItems[index].clm_need_count)){
						this.productItems[index].clm_use_count =Number(this.productItems[index].clm_need_count);
					}else{
						this.productItems[index].clm_use_count= Number(this.productItems[index].clm_material_full_count)-Number(tmp_material_cube_size)+Number(this.productItems[index].clm_use_count);
					}
				}else{
					this.productItems[index].clm_use_count =Number(this.productItems[index].clm_m_before_product_count);
				}
				return;
			}

			if(this.productItems[index].clm_short_count=='' || Number(this.productItems[index].clm_short_count)<=0 ||typeof this.productItems[index].clm_short_count=='undefined' ){
				this.productItems[index].clm_short_count=Number(this.productItems[index].clm_need_count)-tmp_material_order_use_total+Number(this.productItems[index].clm_use_count);
			}
			if( Number(this.productItems[index].clm_use_count) > Number(this.productItems[index].clm_need_count)){
				if(Number(this.productItems[index].clm_m_before_product_count) == '' || isNaN(this.productItems[index].clm_m_before_product_count)==true){
					if(Number(this.productItems[index].clm_material_full_count)-Number(tmp_material_cube_size)+Number(this.productItems[index].clm_use_count)>Number(this.productItems[index].clm_need_count)){
						this.productItems[index].clm_use_count =Number(this.productItems[index].clm_need_count);
					}else{
						this.productItems[index].clm_use_count= Number(this.productItems[index].clm_material_full_count)-Number(tmp_material_cube_size)+Number(this.productItems[index].clm_use_count);
					}
				}else{
					this.productItems[index].clm_use_count =Number(this.productItems[index].clm_m_before_product_count);
				}
				alert('현재고수량이 필요수량을 초과했습니다.');
				return;
