		getproductModalList() {
			let items = axios.get('/back/SCR001001/ProductList',{
				params: {
					search:  encodeURI(this.clm_m_s_product_name),
					search1: '',
					}
				}).then(
				response => {
					items = response.data;
					setTimeout(() => {
						this.m7_document = items
					}, 500)
				},
			)
		},
		onClickProductModalRow(event, data){
			if(data.item.exist_yn =='N'){
				alert( data.item.clm_product_name+'은(는) 부품이 설정되지 않았습니다.')
				return;
			}
			this.stockItems=[{}];
			this.stockItems.splice(0, 1);
			this.product_loading_yn='Y';
			this.clm_m2_product_id = data.item.clm_product_id;
			this.clm_m2_product_name = data.item.clm_product_name;
			let items = axios.get('/back/SCR003009/SelectProductItemsList',{
				params: {
						search: '',
						search2: data.item.clm_product_id,
						search3:this.clm_m2_product_count,
						clm_client_order_id: '',
						clm_joborder_id: ''
						}
				}).then(
						response => {
						items = response.data;
						for(var x=0; x<response.data.length; x++){
							if(response.data[x].clm_material_name == ''){
								this.stockItems.push({
									clm_material_name:'',
									clm_part_id:response.data[x].clm_part_id,
									clm_material_id:'',
									clm_part_name:response.data[x].clm_part_name,
									clm_material_order_id:'',
									clm_order_income_seq:'',
									clm_income_material_seq:'',
									clm_material_leftover_seq:'',
									clm_m_before_product_count:0,
									clm_material_lotno:'',
									clm_need_count : response.data[x].clm_need_count,
									clm_material_full_count : 0,
									clm_use_count:0,
									clm_usable_count:0,
									clm_same_count_yn:response.data[x].clm_same_count_yn,
									clm_half_option_id:response.data[x].tmpHalfOption
								});
							}else{
								this.stockItems.push({
									clm_material_name: response.data[x].clm_material_name +'/'+response.data[x].clm_full_size,
									clm_part_id:response.data[x].clm_part_id,
									clm_material_id:response.data[x].clm_material_id,
									clm_part_name:response.data[x].clm_part_name,
									clm_material_order_id:response.data[x].clm_material_order_id,
									clm_order_income_seq:response.data[x].clm_order_income_seq,
									clm_income_material_seq:response.data[x].clm_income_material_seq,
									clm_material_leftover_seq:response.data[x].clm_material_leftover_seq,
									clm_m_before_product_count:response.data[x].clm_cube_size,
									clm_material_lotno: response.data[x].lotno,
									clm_need_count : response.data[x].clm_need_count,
									clm_material_full_count : response.data[x].clm_full_size,
									clm_use_count:response.data[x].clm_cube_size,
									clm_usable_count:response.data[x].clm_usable_count,
									clm_same_count_yn:response.data[x].clm_same_count_yn,
									clm_half_option_id:response.data[x].tmpHalfOption
								});
							}
							if(x==response.data.length-1){
								this.product_loading_yn='N';
							}
						}
						for(var x=0; x<this.stockItems.length; x++){
							var tmp_material_order_use_total = 0; //동일 부품 적용량
							for(var y=0; y<this.stockItems.length; y++){
								if(this.stockItems[x].clm_part_id == this.stockItems[y].clm_part_id){
									tmp_material_order_use_total = Number(tmp_material_order_use_total) + Number(this.stockItems[y].clm_use_count);
								}
							}
							tmp_material_order_use_total= Number(this.stockItems[x].clm_need_count) - Number(tmp_material_order_use_total);
							this.stockItems[x].clm_short_count = tmp_material_order_use_total;		
						}
					},
				)
			// this.PrdModal=true;
			this.productModal = false;
			this.clm_m_s_product_name = '';
		},
		async StockProductRegPlan(){
			if(this.loading_yn=='Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			var Error_Sum_less='N';
			var Error_Mat_LotNo = 'N';
			this.loading_yn='Y';
			var Error_Mat_Count = 'N';
			var Error_Mat_Count2 = 'N';
			var Error_Sum_over = 'N';
			var materialData = '';
			var error_use='N';
			var cnt =0;

			if(this.clm_m2_product_id == '' || this.clm_m2_product_name == ''){
				alert('제품이 선택되지 않았습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.clm_m2_product_count == '' || Number(this.clm_m2_product_count) < 1){
				alert('제품 수량이 정상적이지 않습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.clm_m_assign_manager_name == ''){
				alert('파트장을 선택해주시기 바랍니다.');
				this.loading_yn='N';
				return;
			}
			if(this.clm_m2_product_location_id == ''){
				alert('제품창고가 선택되지 않았습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.clm_m2_product_location_position_id == ''){
				alert('제품위치가 선택되지 않았습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.stockItems.length == 0){
				alert('제품을 선택해주시기 바랍니다.');
				this.loading_yn='N';
				return;
			}
			if(this.stockItems.length > 0){
				for(var i=0; i<this.stockItems.length; i++){
				var tmp_material_order_use_total=0;//동일 부품 사용량
				var tmp_material_cube_size =0;//동일 자재 사용량
				var tmp_material_count =0;//동일 부품에 자재 있는 개수
					if(materialData != ''){
						materialData += '▒';
					}
					for(var j=0; j<this.stockItems.length; j++){
						if(this.stockItems[i].clm_part_id==this.stockItems[j].clm_part_id){
							tmp_material_order_use_total = Number(tmp_material_order_use_total) + Number(this.stockItems[j].clm_use_count);
						}
						if(this.stockItems[i].clm_material_lotno== this.stockItems[j].clm_material_lotno){
							tmp_material_cube_size = Number(tmp_material_cube_size) + Number(this.stockItems[j].clm_use_count);
						}
						if(this.stockItems[i].clm_part_id==this.stockItems[j].clm_part_id && this.stockItems[i].clm_material_lotno!=''){
							tmp_material_count = Number(tmp_material_count) + 1;
						}
					}
					if(this.stockItems[i].clm_material_lotno == ''){
						Error_Mat_LotNo = 'Y';
					}
					if(this.stockItems[i].clm_material_count == '' ){
						this.stockItems[i].clm_material_count=0;
					}
					
					if((Number(tmp_material_order_use_total)!=0 ||Number(this.stockItems[i].clm_use_count)!=0 ) && Number(tmp_material_order_use_total)!=Number(this.stockItems[i].clm_need_count) && this.stockItems[i].clm_same_count_yn=='Y'){
						// Error_Same_countYN = 'Y';
						alert(this.stockItems[i].clm_part_name+'은(는) 필요수량과 사용수량이 동일해야합니다.');
						this.loading_yn='N';
						return;
					}
					if(Number(this.stockItems[i].clm_use_count) < 0 || Number(this.stockItems[i].clm_use_count)% 1!= 0){
						Error_Mat_Count = 'Y';
					}
					if(Number(this.stockItems[i].clm_material_full_count) < Number(this.stockItems[i].clm_use_count)){
						Error_Mat_Count2 = 'Y';
					}
					
					if(tmp_material_order_use_total > Number(this.stockItems[i].clm_need_count) && this.stockItems[i].clm_same_count_yn=='Y'){
						Error_Sum_over = 'Y';
					}else if(tmp_material_order_use_total < Number(this.stockItems[i].clm_need_count)&& this.stockItems[i].clm_same_count_yn=='Y'){
						Error_Sum_less = 'Y';
					}
					//사용체크
					if(this.stockItems[i].clm_material_id!=''){
						await axios.post('/back/Common/UseCheck', null, { params: {
						clm_joborder_id: this.clm_m2_joborder_id,
						clm_material_id:this.stockItems[i].clm_material_id,
						clm_material_count:this.stockItems[i].clm_material_full_count,
						clm_material_order_id:this.stockItems[i].clm_material_order_id,
						clm_order_income_seq: this.stockItems[i].clm_order_income_seq,
						clm_income_material_seq: this.stockItems[i].clm_income_material_seq,
						clm_material_leftover_seq:this.stockItems[i].clm_material_leftover_seq			
						}})
						.then(response => {
							if (response.status == '200') {
								if(response.data!='N'){
									cnt= i;
									error_use=response.data;
								}
							}
						})
						.catch(error => {
							console.log(error)
						})
					}
				materialData += this.stockItems[i].clm_material_lotno + '▦' + this.stockItems[i].clm_material_order_id + '▦' + this.stockItems[i].clm_order_income_seq + '▦' + this.stockItems[i].clm_income_material_seq + '▦' + this.stockItems[i].clm_material_leftover_seq + '▦' + this.stockItems[i].clm_material_id + '▦' + this.stockItems[i].clm_material_full_count + '▦' + this.stockItems[i].clm_use_count+ '▦' + this.stockItems[i].clm_part_id + '▦' + tmp_material_cube_size + '▦' + tmp_material_count;
				}
			}	
			if(error_use=='Y'){
				alert(this.stockItems[cnt].clm_material_lotno +'은(는) 이미 사용된 자재입니다.');
				this.loading_yn='N';
				return;
			}else if(error_use=='D'){
				alert(this.stockItems[cnt].clm_material_lotno +'은(는) 삭제된 자재입니다.');
				this.loading_yn='N';
				return;
			}else if(error_use=='U'){
				alert(this.stockItems[cnt].clm_material_lotno +'은(는) 수정된 자재입니다.');
				this.loading_yn='N';
				return;
			}
			if(Error_Mat_Count == 'Y'){
				alert('수량이 정상적이지 않은 자재가 있습니다.');
				this.loading_yn='N';
				return;
			}
			if(Error_Mat_Count2 == 'Y'){
				alert('자재가용수량을 초과한 자재가 있습니다.');
				this.loading_yn='N';
				return;
			}
			var mapping_yn = 'N';
			if(Error_Sum_over == 'Y'){
				alert('필요수량보다 현재고수량이 많은 부품이 있습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.clm_m2_request_delivery_date != '' && this.clm_m2_request_delivery_time != ''){
				this.clm_m2_request_delivery_datetime = this.clm_m2_request_delivery_date + ' ' + this.clm_m2_request_delivery_time+':00:00';
			}else{
				this.clm_m2_request_delivery_datetime = '';
			}
			var comment='';
			if(this.clm_m2_request_delivery_datetime != this.clm_m_delivery_request_origin_datetime && Number(this.clm_m2_product_count) == Number(this.clm_m_product_origin_count)){
				comment = '배송요청일시변경('+this.clm_m_delivery_request_origin_datetime+'->'+this.clm_m2_request_delivery_datetime+')';
			}else if(this.clm_m2_request_delivery_datetime == this.clm_m_delivery_request_origin_datetime && Number(this.clm_m2_product_count) != Number(this.clm_m_product_origin_count)){
				comment = '수량변경('+this.clm_m_product_origin_count+'->'+this.clm_m2_product_count+')';
			}else if(this.clm_m2_request_delivery_datetime != this.clm_m_delivery_request_origin_datetime && Number(this.clm_m2_product_count) != Number(this.clm_m_product_origin_count)){
				comment = '수량변경('+this.clm_m_product_origin_count+'->'+this.clm_m2_product_count+'),배송요청일시변경('+this.clm_m_delivery_request_origin_datetime+'->'+this.clm_m2_request_delivery_datetime+')';
			}else{
				comment ='';
			}
			if(this.clm_m_emergency_yn=='Y'){
				comment = '긴급,'+comment;
			}
			if(comment=='긴급,'){
				comment='긴급';
			}
			axios.post('/back/SCR003009/ProductReg2', null, { params: {
				clm_client_order_id:'CO000000',
				clm_joborder_id:this.clm_m2_joborder_id,
				clm_assign_manager_id :this.clm_m_assign_manager,
				clm_client_id: this.clm_m2_client_id,
				clm_product_id: this.clm_m2_product_id,
				clm_request_delivery_datetime:'',
				clm_product_count: Number(this.clm_m2_product_count),
				clm_product_location_id: this.clm_m2_product_location_id,
				clm_product_location_position_id: this.clm_m2_product_location_position_id,
				clm_user: this.$session.get('user_id'),
				clm_comment : comment,
				clm_status:'생산계획',
				materialData: materialData,
				mapping_yn : mapping_yn
			}})
			.then(response => {
				if (response.status == '200') {
					this.loading_yn='N';
					this.PrdModalCLick = false;
					this.selected_items=[];
					this.getList();
					alert('정상적으로 저장되었습니다.');
				}
			})
			.catch(error => {
				console.log(error)
			})
			
		},
		materialModalOpen(index, gubun){
			this.m_s_keyword = '';
			this.material_gubun = gubun;
			if(gubun == 'Detail'){
				this.tmp_index = Number(index);
				this.m_document = [];
				this.getmaterialModalSearch();
				this.materialModal = true;
			}else if(gubun == 'Half'){
				this.tmp_half_index = Number(index);
				if(index=='1'){
					if(this.clm_m2_block_fit_yn=='Y' && this.HalfItems[1].clm_m_material_name!='' && index=='1'){
						this.blockModalOpen();
					}else{
						this.clm_m_s3_material_order='';
						this.clm_m_s2_material_order='';
						this.tmp_value= 'half';
						this.multi_document = [];
						this.m4_document = [];
						this.selected_items3=[];
						this.getmaterialCubeModal2();
						this.materialCubeModal2 = true;	
					}
				}else{
					this.m_document = [];
					this.getmaterialModalSearch();
					this.materialModal = true;
				}
			}
			
			
		},
		deleteHalfItems(index){
			this.MultiHalfItems.splice(index,1);
			var cnt= this.MultiHalfItems.length-1;
			
			if(this.MultiHalfItems.length>1){
				this.HalfItems[0].lotno= this.MultiHalfItems[0].lotno+' 외 '+cnt +'개';
