		product_count_change_ck(){
			if(this.clm_m2_product_count < 1 || Number(this.clm_m2_product_count)% 1!= 0){
				alert('제품 수량이 정상적이지 않습니다.');
				this.clm_m2_product_count=0;
				return;
			}
			if(this.stockItems.length>0){
				for(var x=0;x<this.stockItems.length;x++){
					this.stockItems[x].clm_need_count = Number(this.stockItems[x].clm_need_count)/Number(this.clm_m2_before_product_count);
					this.stockItems[x].clm_need_count = Number(this.stockItems[x].clm_need_count)*Number(this.clm_m2_product_count);				
				}
				for(var x=0; x<this.stockItems.length; x++){
					var max_index=0;
					var cnt=0;
					var cnt2=0;
					var tmp_material_order_use_total = 0; //동일 부품 적용량
					var tmp_material_order_use_total_short =0;
					if(Number(this.stockItems[x].clm_use_count)>Number(this.stockItems[x].clm_need_count)){
						this.stockItems[x].clm_use_count=this.stockItems[x].clm_need_count;
						this.stockItems[x].clm_short_count=0;
					}
					for(var y=0; y<this.stockItems.length; y++){
						if(this.stockItems[x].clm_part_id == this.stockItems[y].clm_part_id){
							tmp_material_order_use_total = Number(tmp_material_order_use_total) + Number(this.stockItems[y].clm_use_count);
							if(max_index < x){
								max_index = x; 
							}
						}								
					}				
					tmp_material_order_use_total_short= Number(this.stockItems[x].clm_need_count) - Number(tmp_material_order_use_total);
					this.stockItems[x].clm_short_count = tmp_material_order_use_total_short;
					if(Number(tmp_material_order_use_total)>Number(this.stockItems[x].clm_need_count)){
						//부품 필요량보다 사용량이 많아
						if(this.stockItems[x].clm_material_name==''){
							this.stockItems.splice(x, 1);
							cnt=cnt+1;
						}
						this.stockItems[x].clm_short_count=0;
						cnt2= max_index+cnt;
						this.stockItems[cnt2].clm_use_count= Number(this.stockItems[cnt2].clm_use_count)- (Number(tmp_material_order_use_total)- Number(this.stockItems[cnt2].clm_need_count));			
					}
				}
				for(var x=0;x<this.stockItems.length;x++){
					this.stock_use_count_change(x);
					
				}
			}
			this.clm_m2_before_product_count = this.clm_m2_product_count;
		},
