		async MultiSaveHalf(){
			if(this.loading_yn=='Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			this.loading_yn='Y';
			if(this.HalfItems[0].lotno ==''){
				alert('자재를 선택해주시기 바랍니다.');
				this.loading_yn='N';
				return;
			}else if(this.m_half_part_user_name=='' || this.m_half_part_user_id==''){
				alert('파트장을 선택해주시기 바랍니다.');
				this.loading_yn='N';
				return;
			}else if(this.m_half_assembly_id=='' || this.m_half_assembly_name==''){
				alert('반제품 설비를 선택해주시기 바랍니다.');
				this.loading_yn='N';
				return;
			}
			var array_material_order_id = '';
			var array_order_income_seq = '';
			var array_income_material_seq = '';
			var array_material_leftover_seq = '';
			var array_material_id = '';
			var array_material_count= '';
			var array_usable_count= '';
			var clm_load_repository='';
			var clm_load_repository_position='';
			var holl_size='';
			var error_use='N';
			var cnt =0;
			var use_yn = 'N';

			var comma='';
			var comma2='';
			var name ='';
			var code ='';
			var half_product_option ='';
			var half_product_name ='';		
			for(var j=0;j<6;j++){
				code='';
				name='';
				if(half_product_name!=''){
					comma=',';
				}
				if(half_product_option!=''){
					comma2=',';
				}
				if(this.clm_m2_half_fit_yn=='Y' && j==0){
					code='0001';
					name='반조립';
				}
				if(this.clm_m2_gui_yn=='Y'  && j==1){
					code='0002';
					name='귀날림';
				}
				if(this.clm_m2_hom_yn=='Y' && j==2){
					code='0003';
					name='홈가공';
				}
				if(this.clm_m2_bolt_yn=='Y' && j==3){
					code='0004';
					name='볼트작업';
				}
				if(this.clm_m2_holl_yn=='Y' && j==4){
					code='0005';
					name='홀가공';
				}
				if(this.clm_m2_block_fit_yn=='Y' && j==5){
					code='0006';
					name='블록조립';
				}	
				if(name!=''){
					half_product_name= half_product_name+comma+name;
				}
				if(code!=''){
					half_product_option = half_product_option+comma2+code;
				}				
			}
			if(half_product_name.charAt(0)==','){
				half_product_name.substr(1);
			}
			if(half_product_option.charAt(0)==','){
				half_product_option.substr(1);
			}

			for(var i=0; i<this.MultiHalfItems.length; i++){				
				array_material_order_id = array_material_order_id + this.MultiHalfItems[i].clm_material_order_id + '▦';
				array_order_income_seq= array_order_income_seq+ this.MultiHalfItems[i].clm_order_income_seq + '▦';
				array_income_material_seq = array_income_material_seq + this.MultiHalfItems[i].clm_income_material_seq + '▦';
				array_material_leftover_seq = array_material_leftover_seq + this.MultiHalfItems[i].clm_material_leftover_seq + '▦';
				array_material_id = array_material_id + this.MultiHalfItems[i].clm_material_id + '▦';
				array_material_count = array_material_count + this.MultiHalfItems[i].clm_order_count + '▦';
				array_usable_count = array_usable_count + this.MultiHalfItems[i].clm_full_count + '▦';
				
				await axios.post('/back/Common/UseCheck', null, { params: {
					clm_joborder_id: '',
					clm_material_id: this.MultiHalfItems[i].clm_material_id,
					clm_material_count: this.MultiHalfItems[i].clm_full_count,
					clm_material_order_id: this.MultiHalfItems[i].clm_material_order_id,
					clm_order_income_seq: this.MultiHalfItems[i].clm_order_income_seq,
					clm_income_material_seq: this.MultiHalfItems[i].clm_income_material_seq,
					clm_material_leftover_seq: this.MultiHalfItems[i].clm_material_leftover_seq				
				}})
				.then(response => {
					if (response.status == '200') {
						if(response.data!='N'){
							cnt= i;
							error_use=response.data;
						}
					}
				})
				.catch(error => {
					console.log(error)
				})
			}
			if(error_use=='Y'){
				alert(this.MultiHalfItems[cnt].lotno +'은(는) 이미 사용된 자재입니다.');
				this.loading_yn='N';
				return;
			}else if(error_use=='D'){
				alert(this.MultiHalfItems[cnt].lotno +'은(는) 삭제된 자재입니다.');
				this.loading_yn='N';
				return;
			}else if(error_use=='U'){
				alert(this.MultiHalfItems[cnt].lotno +'은(는) 수정된 자재입니다.');
				this.loading_yn='N';
				return;
			}
			
			array_material_order_id = array_material_order_id.substring(0, array_material_order_id.length-1);
			array_order_income_seq = array_order_income_seq.substring(0, array_order_income_seq.length-1);
			array_income_material_seq = array_income_material_seq.substring(0, array_income_material_seq.length-1);
			array_material_leftover_seq = array_material_leftover_seq.substring(0, array_material_leftover_seq.length-1);
			array_material_id = array_material_id.substring(0, array_material_id.length-1);
			array_material_count = array_material_count.substring(0, array_material_count.length-1);
			array_usable_count = array_usable_count.substring(0, array_usable_count.length-1);

			axios.post('/back/SCR004005/RegMultiHalfProduct', null, { params: {
				array_material_order_id: array_material_order_id,
				array_order_income_seq:array_order_income_seq,
				array_income_material_seq: array_income_material_seq,
				array_material_leftover_seq: array_material_leftover_seq,
				array_material_id: array_material_id,
				array_material_count: array_material_count,
				array_usable_count: array_usable_count,
				clm_user_id1 : this.m2_user_id1,
				clm_user_id2 : this.m2_user_id2,
				clm_user_id3 : this.m2_user_id3,
				clm_user_id4 : this.m2_user_id4,
				clm_user_id5 : this.m2_user_id5,
				clm_user_id6 : this.m2_user_id6,
				clm_half_option_id:half_product_option,
				clm_half_product_option_name :half_product_name,
				clm_assembly_id : this.m_half_assembly_id,
				clm_part_user_id : this.m_half_part_user_id,
				clm_user:this.$session.get('user_id')
			}})
			.then(response => {
				if (response.status == '200') {
					this.loading_yn='N';
					// 상태 업데이트
					axios.post('/back/MOB001001/JobOrderUpdateStatus', null, { params: {
						clm_joborder_id: this.m_joborder_id,
						clm_client_order_id: this.m_client_order_id,
						clm_status: '반제품작업',
						clm_user: this.user_id
					}})
					.then(response => {
						if (response.status == '200') {
							this.loading_yn='N';
							// alert('정상적으로 변환 되었습니다.');
							if(confirm("정상적으로 변환 되었습니다.\n생산계획을 저장하시겠습니까?")){
								this.ProductSave();
							}
							this.halfModal = false;
							// this.DetailModal = false;
							this.getList();
						}
					})
				}
			})
			.catch(error => {
				console.log(error)
			})
		},
