					clm_order_income_seq: item.clm_order_income_seq,
					clm_income_material_seq:item.clm_income_material_seq,
					clm_material_leftover_seq: item.clm_material_leftover_seq
					}})
					.then(response => {
						if (response.status == '200') {
							usable_count= response.data;
							if(Err_JongBok == 'N'){
								this.productItems[this.tmp_index].clm_material_lotno =  item.lotno;
								this.productItems[this.tmp_index].clm_material_order_id =  item.clm_material_order_id;
								this.productItems[this.tmp_index].clm_order_income_seq =  item.clm_order_income_seq;
								this.productItems[this.tmp_index].clm_income_material_seq =  item.clm_income_material_seq;
								this.productItems[this.tmp_index].clm_material_leftover_seq =  item.clm_material_leftover_seq;
								this.productItems[this.tmp_index].clm_material_id =  item.clm_material_id;
								this.productItems[this.tmp_index].clm_material_name =  item.clm_material_name + ' / ' + item.clm_cube_size;			
								this.productItems[this.tmp_index].clm_material_full_count =  Number(item.clm_cube_size.replace(/,/gi, ""));
								this.productItems[this.tmp_index].clm_load_repository =  item.clm_load_repository;
								this.productItems[this.tmp_index].clm_load_repository_position =  item.clm_load_repository_position;
