		delPrdMat(index){
			if(this.productItems[index].clm_part_id != ''){
				
				var tmp_material_order_use_total = 0; //같은 lotno 
				var part_double_ck=0;
				var exist_yn= 'N';
				var max_index=index;
				for(var i=0; i<this.productItems.length; i++){					
					if(this.productItems[index].clm_part_name == this.productItems[i].clm_part_name ){
						}
					if(this.productItems[index].clm_part_id==this.productItems[i].clm_part_id){
						part_double_ck++;
						tmp_material_order_use_total = Number(tmp_material_order_use_total) + Number(this.productItems[i].clm_use_count);
					}		
				}
				if(part_double_ck==1){
					// alert('부품당 1개 이하는 삭제 할 수 없습니다.');
					if(confirm('부품 당 1개 이하는 삭제 할 수 없습니다.\n자재정보를 삭제하시겠습니까?')){
						this.productItems[index].clm_material_lotno='';
						this.productItems[index].clm_material_order_id='';
						this.productItems[index].clm_order_income_seq='';
						this.productItems[index].clm_income_material_seq='';
						this.productItems[index].clm_material_leftover_seq='';
						this.productItems[index].clm_material_id='';
						this.productItems[index].clm_material_name='';
						this.productItems[index].clm_material_full_count='';
						this.productItems[index].clm_use_count=0;
						this.productItems[index].clm_usable_count='';
						this.productItems[index].clm_short_count=this.productItems[index].clm_need_count;
					}
					return;
				}
				for(var i=0; i<this.productItems.length; i++){	
					if(this.productItems[index].clm_part_id==this.productItems[i].clm_part_id){
						this.productItems[i].clm_short_count = Number(this.productItems[i].clm_short_count) + Number(this.productItems[index].clm_use_count);
					}
					if(this.productItems[index].clm_part_id==this.productItems[i].clm_part_id && this.productItems[i].clm_material_name == '' && index != i || this.productItems[i].clm_short_count){
						exist_yn = 'Y';
					}
					if(this.productItems[index].clm_part_id==this.productItems[i].clm_part_id){
						if(max_index < i){
							max_index = i; 
						}
					}
				}
				if(exist_yn != 'Y'){
					if(index+1 == this.productItems.length ){//마지막줄일경우 .push로 추가
						this.productItems.splice(index+1, 0, {
							clm_part_name:this.productItems[index].clm_part_name,
							clm_part_id:this.productItems[index].clm_part_id,
							clm_material_lotno : '',
							clm_material_order_id : '',
							clm_order_income_seq : '',
							clm_income_material_seq : '',
							clm_material_leftover_seq : '',
							clm_material_id : '',
							clm_material_name : '',
							clm_material_full_count : 0,
							clm_need_count:this.productItems[index].clm_need_count,
							clm_use_count : 0,
							clm_half_option_id:this.productItems[index].clm_half_option_id,
							clm_short_count : this.productItems[index].clm_short_count,
							clm_same_count_yn : this.productItems[index].clm_same_count_yn,
							clm_load_repository : '',
							clm_load_repository_position : ''
						});		
					} else {					
						this.productItems.splice(max_index+1, 0, {
							clm_part_name:this.productItems[index].clm_part_name,
							clm_part_id:this.productItems[index].clm_part_id,
							clm_material_lotno : '',
							clm_material_order_id : '',
							clm_order_income_seq : '',
							clm_income_material_seq : '',
							clm_material_leftover_seq : '',
							clm_material_id : '',
							clm_material_name : '',
							clm_material_full_count : 0,
							clm_need_count:this.productItems[index].clm_need_count,
							clm_use_count : 0,
							clm_half_option_id:this.productItems[index].clm_half_option_id,
							clm_short_count : this.productItems[index].clm_short_count,
							clm_same_count_yn : this.productItems[index].clm_same_count_yn,
							clm_load_repository : '',
							clm_load_repository_position : ''
						});
					}
				}
			}
			if(this.productItems.length == 1){
				alert('1개 이하는 삭제 할 수 없습니다.');
			}else{
				this.productItems.splice(index,1);
			}
