		fnc_request(index){
			var msg_title = this.m_joborder_id + ' 자재 구매 요청';
			var msg_text = '제품 ' + this.m_product_name + '의 부품 ' + this.productItems[index].clm_part_name + '을(를) 구매 요청드립니다.';
			var msg_sender_id = this.user_id;

			if(confirm('제품 ' + this.m_product_name + '의 부품 ' + this.productItems[index].clm_part_name + '을(를) 구매요청 하시겠습니까?')){
				axios.get('/back/MOB001001/SelectBuyMaterialMSG',{
					params: {
						msg_title: msg_title,
						msg_text: msg_text,
						msg_sender_id: msg_sender_id,
						msg_receive_id: this.m_receive_user
					}
				})
				.then(response => {
					if(response.data == ''){
						axios.post('/back/MOB001001/BuyMaterialMSG', null, { params: {
							msg_title: msg_title,
							msg_text: msg_text,
							msg_sender_id: msg_sender_id,
							msg_receive_id: this.m_receive_user
						}})
						.then(response => {
							// 상태 업데이트
