								clm_joborder_id: this.m_joborder_id,
								clm_client_order_id: this.m_client_order_id,
								clm_status: '구매요청',
								clm_user: this.user_id
							}})
							.then(response => {
								if (response.status == '200') {
									alert('정상적으로 구매요청 되었습니다.');
									// this.DetailModal = false;
									this.getList();
								}
							})
						});
					}else{
						alert(response.data + '에 구매 요청 내역이 있습니다.\n잠시 후 다시 요청 부탁드립니다.');
