		async assemblyLastSave(){
			if(this.loading_yn=='Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			this.loading_yn='Y';
			var materialData = '';
			var error_use='N';
			var cnt =0;
			if(this.m_assemblyuserid == '' || this.m_assemblyusername == ''){
				alert('조립담당자가 등록되지 않아 조립작업을 등록 할 수 없습니다.');
				this.loading_yn='N';
