							if (response.status == '200') {
								if(response.data!='N'){
									cnt= i;
									error_use=response.data;
								}
							}
						})
						.catch(error => {
							console.log(error)
						})
					}
					materialData += this.productItems[i].clm_material_lotno + '▦' + this.productItems[i].clm_material_order_id + '▦' + this.productItems[i].clm_order_income_seq + '▦' + this.productItems[i].clm_income_material_seq + '▦' + this.productItems[i].clm_material_leftover_seq + '▦' + this.productItems[i].clm_material_id + '▦' + this.productItems[i].clm_material_full_count + '▦' + this.productItems[i].clm_use_count+ '▦' + this.productItems[i].clm_part_id + '▦' + tmp_material_cube_size  + '▦' + this.productItems[i].clm_load_repository + '▦' + this.productItems[i].clm_load_repository_position;
