		async ChecksaveUser(){
			var id= '';
			var name ='';
			for(var x=0;x<this.user_document_1.length;x++){
				if(this.user_document_1[x].clm_check_yn=='Y'){
					id=id+this.user_document_1[x].clm_user_id+',';
					name=name+this.user_document_1[x].clm_user_name+',';
				}
			}
			for(var x=0;x<this.user_document_2.length;x++){
				if(this.user_document_2[x].clm_check_yn=='Y'){
					id=id+this.user_document_2[x].clm_user_id+',';
					name=name+this.user_document_2[x].clm_user_name+',';
				}
			}
			for(var x=0;x<this.user_document_3.length;x++){
				if(this.user_document_3[x].clm_check_yn=='Y'){
					id=id+this.user_document_3[x].clm_user_id+',';
					name=name+this.user_document_3[x].clm_user_name+',';
				}
			}
			for(var x=0;x<this.user_document_4.length;x++){
				if(this.user_document_4[x].clm_check_yn=='Y'){
					id=id+this.user_document_4[x].clm_user_id+',';
					name=name+this.user_document_4[x].clm_user_name+',';
				}
			}
			if(id == '' || name ==''){
				alert('선택된 작업자가 없습니다.');
				return;
			}
			var count = id.split(',').length - 1;

			if(count==6){
				alert('작업자는 5명까지 등록가능합니다');
				return;
			}
			id = id.slice(0, -1);
			name =name.slice(0, -1);

			if(this.user_gubun!='half'){
				if(this.user_gubun == 'Assembly'){
					this.m_assemblyusername = name;
					this.m_assemblyuserid = id;
				}else if(this.user_gubun == 'Cut'){
					this.clm_m3_user_name =  name;
					this.clm_m3_user_id = id;
					this.m_cut2_user_name =  name;
					this.m_cut2_user_id =id;
				}
			}else{
				if(this.tmp_cnt==1){
					this.m2_user_name1 = name;
					this.m2_user_id1 = id;
				}else if(this.tmp_cnt==2){
					this.m2_user_name2= name;
					this.m2_user_id2= id;
				}else if(this.tmp_cnt==3){
					this.m2_user_name3 = name;
					this.m2_user_id3 = id;
				}else if(this.tmp_cnt==4){
					this.m2_user_name4 = name;
					this.m2_user_id4 =id;
				}else if(this.tmp_cnt==5){
					this.m2_user_name5 = name;
					this.m2_user_id5 =id;
				}else if(this.tmp_cnt==6){
					this.m2_user_name6 = name;
					this.m2_user_id6 =id;
				}
			}
			this.CheckuserModal=false;
		},
