		async saveHalfMaterialReg(){
			if(this.loading_yn=='Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			this.loading_yn='Y';
			if(this.m_halfm_material_type == ''){
				alert('반제품 유형이 빈칸입니다.');
				this.loading_yn='N';
				return;
			}
			if(this.m_halfm_material_width == ''){
				alert('반제품 길이가 빈칸입니다.');
				this.loading_yn='N';
				return;
			}
			if(Number(this.m_halfm_material_width ) <=0){
				alert('반제품 길이가 0또는 음수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}
			if(this.m_halfm_material_height == ''){
				alert('반제품 넓이가 빈칸입니다.');
				this.loading_yn='N';
				return;
			}
			if(Number(this.m_halfm_material_height) <=0){
				alert('반제품 넓이가 0또는 음수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}
			if( this.clm_m_s_material_thickness == '' ){
				alert('반제품 두께가 빈칸입니다.');
				this.loading_yn='N';
				return;
			}
			if(Number(this.m_halfm_material_thickness) <=0){
				alert('반제품 두께가 0또는 음수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}
			if(Number( this.m_halfm_material_holl_size )<0){
				alert('반제품 홀크기가 음수가 될 수 없습니다.');
				this.loading_yn='N';
				return;
			}
			if( this.m_halfm_material_holl_size == '' ){
				this.m_halfm_material_holl_size=0;
			}
			var comma = '';
			var comma2 = '';
			var name = '';
			var code = '';
			var half_product_option = '';
			var half_product_name = '';		
			for(var j=0;j<6;j++){
				code = '';
				name = '';
				if(half_product_name != ''){
					comma = ',';
				}
				if(half_product_option != ''){
					comma2 = ',';
				}
				if(this.clm_m2_half_fit_yn == 'Y' && j==0){
					code = '0001';
					name = '반조립';
				}
				if(this.clm_m2_gui_yn == 'Y' && j==1){
					code = '0002';
					name = '귀날림';
				}
				if(this.clm_m2_hom_yn == 'Y' && j==2){
					code = '0003';
					name = '홈가공';
				}
				if(this.clm_m2_bolt_yn == 'Y' && j==3){
					code = '0004';
					name = '볼트작업';
				}
				if(this.clm_m2_holl_yn == 'Y' && j==4){
					code = '0005';
					name = '홀가공';
				}
				if(this.clm_m2_block_fit_yn == 'Y' && j==5){
					code = '0006';
					name = '블록조립';
				}	
				if(name != ''){
					half_product_name = half_product_name + comma + name;
				}
				if(code != ''){
					half_product_option = half_product_option + comma2 + code;
				}				
			}
			if(half_product_name.charAt(0)==','){
				half_product_name.substr(1);
			}
			if(half_product_option.charAt(0)==','){
				half_product_option.substr(1);
			}

			await axios.post('/back/SCR004005/SubHalfReg', null, { params: {
				clm_m_material_type : this.m_halfm_material_type,
				clm_m_s_material_width : this.m_halfm_material_width,
				clm_m_s_material_height : this.m_halfm_material_height,
				clm_m_s_material_thickness : this.m_halfm_material_thickness,
				clm_wood_id : this.m_halfm_wood_id,
				half_product_name : half_product_name,
				half_product_option : half_product_option,
				clm_user : this.user_id
			}})
			.then(response => {
				if (response.status == '200') {
					this.loading_yn='N';
					if( Number(this.m_halfm_material_holl_size)!=0 &&  this.m_halfm_material_holl_size!=''&& typeof this.m_halfm_material_holl_size!='undefined'){
						this.m_half_product_name = response.data.clm_material_type+' '+response.data.name+' ∅'+this.m_halfm_material_holl_size;
					}else{
						this.m_half_product_name = response.data.clm_material_type+' '+response.data.name
					}
					// this.m_half_product_name = response.data.clm_material_type + ' ' + response.data.name,
					this.m_half_option_id = half_product_option,
					this.m_half_width = this.m_halfm_material_width,
					this.m_half_height = this.m_halfm_material_height,
					this.m_half_holl_size = Number(this.m_halfm_material_holl_size);
					this.m_half_thickness = this.m_halfm_material_thickness;
					// this.m_half_product_count = this.HalfItems[0].clm_m_material_count;
					var cnt =0;
					if(this.HalfItems.length==1){
						cnt=Number(this.HalfItems[0].clm_m_material_count);
					}else{
						if(Number(this.HalfItems[0].clm_m_material_count)<Number(this.HalfItems[1].clm_m_material_count)){
							cnt=Number(this.HalfItems[0].clm_m_material_count);
						}else{
							cnt=Number(this.HalfItems[1].clm_m_material_count);
						}
					}
					this.m_half_product_count =cnt;
					this.m_half_id = response.data.id,
					axios.get('/back/SCR004005/ReadHalfPartUser',{
						params: {
							clm_half_id: this.m_half_id
							}
						}).then(
						response => {		
							if(this.m_half_part_user_name=='' && this.m_half_part_user_id==''){
								this.m_half_part_user_name=response.data.part_user_name ;
								this.m_half_part_user_id=response.data.part_user_id ;
							}
							if(this.m_half_assembly_name=='' && this.m_half_assembly_id==''){
								this.m_half_assembly_name=response.data.assembly_name ;
								this.m_half_assembly_id=response.data.assembly_id ;
							}
						},
					)
					this.m_halfm_material_type = '',
					this.m_halfm_material_type_name = '',
					this.m_halfm_material_width = '',
					this.m_halfm_material_height = '',
					this.m_halfm_material_thickness = '',
					this.m_halfm_material_holl_size = '',
					this.m_halfm_wood_id = '',
					this.halfMateiralModal = false
				}
			})
			.catch(error => {
				console.log(error)
			})
			
		},
